<template>
    <v-container class="ink-component">
        <v-row class="main-content" id="mainContent" ref="mainContent">
        <v-fade-transition>
            <v-col class="col-12 col-md-8 images" v-if="isPlay" :key="isPlay">
                <v-sheet
                    color="white"
                    elevation="0"
                    :class="`content-sheet ${this.$vuetify.breakpoint.name}`"
                    :style="{height: sheetHeight + 'px'}"
                    :key="sheetHeight" id="contentSheet" ref="contentSheet"
                    >
                        <v-fade-transition>
                            <v-progress-linear :class="`${this.$vuetify.breakpoint.name}`" v-if="background == 'title' && !imagesLoaded"
                                :value="progressValue * 100 / 40" buffer-value="100" height="10" striped
                                rounded color="#3f55a3" background-color="#eff2f4" background-opacity="1">
                            </v-progress-linear>
                        </v-fade-transition>
                        <div class="image-panel" id="imagePanel" ref="imagePanel" :key="sheetHeight">
                            <v-slide-x-transition>
                                <v-img :src="`${publicPath}${character}.png`" v-if="characterVisible"
                                    contain class="character" :key="character" />
                            </v-slide-x-transition>
                            <v-slide-x-transition>
                                <v-img :src="`${publicPath}${fullRight}`" v-if="rightVisible"
                                    contain class="right-item" :key="right" />
                            </v-slide-x-transition>
                            <v-slide-x-transition>
                                <v-img :src="`${publicPath}${itemName}.png`" v-if="itemNameVisible"
                                    contain class="item" :key="itemName" />
                            </v-slide-x-transition>
                            <v-fade-transition>
                                <div class="character-bg" v-if="characterVisible || itemNameVisible"></div>
                            </v-fade-transition>
                            <v-fade-transition>
                                <div class="background-frame">
                                    <v-img :src="fullBackground" :key="fullBackground" class="background backgroundFrame"
                                        ref="backgroundFrame" id="backgroundFrame" />
                                </div>
                            </v-fade-transition>
                        </div>
                    </v-sheet>
            </v-col>
            </v-fade-transition>
            <v-fade-transition>
                <v-col class="col-12 col-md-8" v-if="isOver" :key="isOver">
                    <v-sheet
                        color="white"
                        elevation="0"
                        outlined
                        rounded
                        class="ending-sheet"
                        v-show="isOver"
                        id="scoreSheet"
                        ref="scoreSheet"
                        >
                        <v-img :src="`${publicPath}stars.png`" :class="`stars ${this.$vuetify.breakpoint.name}`"></v-img>
                        <div :class="`image-panel ${this.$vuetify.breakpoint.name}`" id="imagePanel" ref="imagePanel" :style="{height: sheetHeight + 'px !important'}" :key="sheetHeight">
                        </div>
                        <div :class="`${this.$vuetify.breakpoint.name} score-sheet`">
                            <div class="achievements"><span class="achievement score-title">Your Achievements: </span>
                                <span v-for="achievement in endStates.achievements" v-bind:key="achievement.name" class="achievement">
                                    <v-tooltip top>
                                        <template  v-slot:activator="{ on, attrs }">
                                            <span v-bind="attrs" v-on="on">{{achievement.name}} <v-icon color="#1b213b">info</v-icon></span>
                                        </template>
                                        {{achievement.description}}
                                    </v-tooltip>
                                </span>
                            </div>
                            <div class="score-table">
                                <v-row>
                                    <v-col class="col-12 col-md-4 left-results">
                                        <div class="score-block revenue rounded-lg">
                                            <div class="data-logo revenue"><v-img :src="`${publicPath}budget.png`"></v-img></div>
                                            <div class="score-title">Profit/Loss</div>
                                            <div class="score-value">{{endStates.states.profits}}</div>
                                        </div>
                                        <div class="score-block morale rounded-lg">
                                            <div class="data-logo morale"><v-img :src="`${publicPath}morale.png`"></v-img></div>
                                            <div class="score-title">Morale</div>
                                            <div class="score-value">{{endStates.states.morale_state}}</div>
                                        </div>
                                        <div class="score-block budget rounded-lg">
                                            <div class="data-logo budget"><v-img :src="`${publicPath}profits.png`"></v-img></div>
                                            <div class="score-title">Budget</div>
                                            <div class="score-value">{{endStates.states.budget_state}}</div>
                                        </div>
                                        <div class="score-block platforms rounded-lg">
                                            <div class="score-title">Platforms</div>
                                            <div class="score-value"><div v-for="platform in endStates.states.platforms" v-bind:key="platform.name" class="platform">
                                                <span class="checkbox" v-if="platform.on">
                                                    <v-icon color="#1b213b">check_box</v-icon>
                                                </span><span class="checkbox" v-else>
                                                    <v-icon color="#1b213b">check_box_outline_blank</v-icon>
                                                </span><span class="platform-name">{{platform.name}}</span>
                                            </div></div>
                                        </div>
                                    </v-col>
                                    <v-col class="col-12 col-md-8 right-results">
                                        <div class="score-block rounded-lg"><div class="score-row">
                                            <div class="score-title">Summary</div>
                                            <div class="points score-value">{{endStates.states.general_description}}</div>
                                        </div></div>
                                        <div class="score-block rounded-lg"><div class="score-row">
                                            <div class="score-title">Release</div>
                                            <div class="release score-value">{{endStates.states.releases}}</div>
                                        </div></div>
                                        <div class="score-block rounded-lg"><div class="score-row">
                                            <div class="score-title">Crunch</div>
                                            <div class="crunch score-value">{{endStates.states.crunch}}</div>
                                        </div></div>
                                        <div class="score-block rounded-lg"><div class="score-row">
                                            <div class="score-title">Scandals</div>
                                            <div class="scandals score-value">{{endStates.states.scandals}}</div>
                                        </div></div>
                                    </v-col>
                                </v-row>
                            </div>
                        </div>
                    </v-sheet>
                </v-col>
            </v-fade-transition>
            <v-fade-transition>
                <v-col class="col-12 col-md-4 text-column" v-if="isPlay">
                    <div class="current-text">
                        <p v-for="displayText in currentText" v-bind:key="displayText.id" v-html="displayText.text"></p>
                    </div>
                    <div class="choices">
                        <v-card v-for="choice in choices()" v-bind:key="choice.index" index class="choice-item">
                            <v-card-text v-on:click="choose(choice.index)">{{choice.text}}</v-card-text>
                        </v-card>
                    </div>
                </v-col>
            </v-fade-transition>
            <v-fade-transition>
                <v-col class="col-12 col-md-4 end-column" v-if="isOver">
                    <div class="choices">
                        <v-card class="choice-item">
                            <v-card-text v-on:click="_restart()">Play again</v-card-text>
                        </v-card>
                        <v-card class="choice-item">
                            <v-card-text v-on:click="gotoNewWindow('https://tinyhydra.com/crunch/')">Learn more about Crunch</v-card-text>
                        </v-card>
                        <v-card class="choice-item">
                            <v-card-text v-on:click="gotoNewWindow('https://tinyhydra.com/quote/')">Hire Tiny Hydra</v-card-text>
                        </v-card>
                    </div>
                </v-col>
            </v-fade-transition>
        </v-row>
    </v-container>
</template>

<script>
import { Story } from '../../node_modules/inkjs/engine/Story';
import Preload from 'image-preload';
import axios from 'axios';

/**
 * The amount of time in millis that images like characters or items take to fade.
 */
const FADE_DELAY = 500;
const ENDGAME_TEXT = "ENDGAME:";
const ENDING_ARRAY = ['achievements', 'profits', 'morale_state', 'budget_state', 'platforms', 'general_description', 'releases', 'crunch', 'scandals'];
const BUDGET = 'budget';
/**
 * @type {object} How each morale integer value should be displayed in the game text.
 */

const MORALE_TABLE = {
    1: 'mutiny',
    2: 'low',
    3: 'medium',
    4: 'high',
    5: 'ecstatic',
    6: 'ecstatic',
}

/**
 * type {Set} The actions that can be called from tagsu
 */

const tagActions = new Set(['bg', 'char', 'dis', 'item', 'gameover']);

export default {
    name: 'InkStory',
    data: function() {
        return {
            imagesLoaded: false,
            progressValue: 0,
            sheetHeight: null,
            story: null,
            budget: 4,
            publicPath: process.env.BASE_URL,
            currentText: [{id: 0, text: ''}],
            currentTags: new Set(),
            background: 'title',
            morale: 'high',
            specialBackground: new Set(['office']),
            backgroundExtension: '',
            fullBackground: 'title.png',
            character: '',
            characterVisible: false,
            itemName: '',
            itemNameVisible: false,
            isOver: false,
            isPlay: true,
            isMounted: false,
            endStates: {},
            right: 'budget',
            rightVisible: false,
            fullRight: 'budget_4.png',
            playInterface: 300,
            playInterfaceHeight: null
        };
    },
    methods: {
        /**
         * Advance into the story.
         * Should be called at the start of the game and after each choice.
         */
        advance: function() {
            let index = 0;
            this.currentText = [];
            this.currentTags = new Set();
            let that = this;
            while (this.story.canContinue && this.story.currentChoices.length === 0) {
                that.story.Continue();
                if (this.story.currentText.length > 1) {
                    for (let tag of that.story.currentTags) {
                        that.currentTags.add(tag);
                    }
                    if (that.currentTags.size > 0) {
                        that.runTags();
                    }
                    let text = that.story.currentText.trim();
                    if (text.startsWith(ENDGAME_TEXT)) {
                        this.gameover(text);
                        continue;
                    }
                    that.currentText.push({id: index, text: that.story.currentText});
                    index += 1;
                }
            }
            this.updateRight();
            let morale = that._elementsFromMap(that.story.variablesState.morale_state);
            that.morale = MORALE_TABLE[morale[0]];
            that.bg(this.background);
        },
        /**
         * Update the bottom-right side image.
         */
        updateRight: function() {
            if (this.right === BUDGET) {
                this.fullRight = `${this.right}_${this._elementsFromMap(this.story.variablesState.budget_state)[0]}.png`;
            } else {
                this.fullRight = this.right + '.png';
            }
        },
        /**
         * Extract all the current Ink choices from the story and return them as an array.
         * @returns {array} The current choices an an array.
         */
        choices: function() {
            if (this.story === null) {
                return [];
            }
            let currentChoices = [];
            for (const choice of this.story.currentChoices) {
                currentChoices.push({index: choice.index, choice: choice, text: choice.text});
            }
            return currentChoices;
        },
        /**
         * Advance the story based on the given choice index.
         * @param {integer} choice integer of the choice to follow
         */
        choose: function(index) {
            this.story.ChooseChoiceIndex(index);
            this.advance();
        },
        /**
         * @returns the height of the score box onscreen in mobile mode.
         */
        scoreDims: function() {
            return [
                `${window.innerHeight - document.getElementsByClassName('main-content')[0].offsetTop + 72}px`,
                `${window.innerWidth + 48}px`];
        },
        /**
         * Get a JS map of one item and return it as a value and a name. Used for Ink list items.
         * @param {ListItem} oneItemMap One list item of an Ink list
         * @returns {array} The value of the item as an integer and the name of the item as a string
         */
        _elementsFromMap: function(oneItemMap) {
            let result = [];
            oneItemMap.forEach(function(value, name) {
                name = JSON.parse(name);
                name = name.itemName;
                result = [value, name];
            });
            return result;
        },
        /**
         * Returns the string to complete `background` attribute of the current instance based on whether there
         * is a need to.
         * @returns {string} An empty string or one containing what is needed to complete the `background` attribute.
         */
        updateBackgroundExtension: function() {
            if (this.specialBackground.has(this.background)) {
                let moraleNumber = this._elementsFromMap(this.story.variablesState.morale_state)[0];
                return `_${MORALE_TABLE[moraleNumber]}`;
            }
            return '';
        },
        /**
         * Update the `backround`, `backgroundExtension`, and `fullBackground` attributes.
         * Called when advancing and by tags.
         * @param {string} background
         */
        bg: function(background) {
            let newBackgroundExtension = this.updateBackgroundExtension();
            if (this.background != background || this.backgroundExtension != newBackgroundExtension) {
                this.background = background.split('.').map(w => w.trim())[0];
                this.backgroundExtension = newBackgroundExtension;
                this.fullBackground = `${this.background}${this.backgroundExtension}.png`;
            }
        },
        item: function (name) {this._showHide(name, false)},
        char: function (name) {
            this._showHide(name, true);
        },
        /**
         * Updates the illustrations. This will remove any previously displayed character or item beforehand. All
         * illstration files must have a `.png` extension.
         * Called by tags.
         * @param {string} name Name of the character or item to display. It may contain the `.png` extension, but that's not mandatory.
         * @param {boolean} character If true, display a "character" otherwise, display an "item".
         */
        _showHide: function(name, character) {
            let me = character ? 'character' : 'itemName';
            let other = character ? 'itemName' : 'character';
            if (name.includes('.png')) {
                name = name.split('.').map(w => w.trim())[0];
            }
            if (this[other + 'Visible']) {
                this[other + 'Visible'] = false;
                this._showWithDelay(name, me);
            } else if (this[me + 'Visible'] && this[me] !== name) {
                this[me + 'Visible'] = false;
                this._showWithDelay(name, me);
            } else {
                this[me] = name;
                this[me + 'Visible'] = true;
            }
        },
        /**
         * Called from `_showHide`. Hides and displayes items and characters after a delay.
         * @param {string} name The name of the item or character to display.
         * @param {string} me The type to display item or character.
         */
        _showWithDelay: function(name, me) {
            let that = this;
            setTimeout(function() {
                that[me] = name;
                setTimeout(() => { that[me + 'Visible'] = true; }, FADE_DELAY);
            }, FADE_DELAY);
        },
        /**platforms: [{name: "PC", on: true}, {name: "console", on: false}, {name: "Next Gen", on: false}]
         */
        dis: function() {
            this.characterVisible = false;
            this.itemNameVisible = false;
        },
        /**
         * Executes every tag currently in the game state. If its name matches a function, then that function is called.
         * The format is: functionName:arg1,arg2,arg3,arg4,...
         */
        runTags: function() {
            for (const tag of this.currentTags) {
                let args = tag.split(':');
                if (args[0] === 'right' && args[1].trim() === 'budget') {
                    this.rightVisible = true;
                    return;
                }
                if (!tagActions.has(args[0])) {
                    console.warn('Unknown tag', args[0]);
                    continue;
                }
                if (args.length < 2) {
                    args.push('');
                }
                if (typeof this[args[0]] === 'function') {
                    this[args[0]].apply(this, args[1].split(','));
                }
            }
        },
        /**
         * Marks the game as over and shows the game over screen.
         * Called by tags.
         */
        gameover: function(text) {
            this.isPlay = false;
            let that = this;
            setTimeout(function() { that.isOver = true; }, FADE_DELAY);
            this._setEndGame(text);
            let negative = false;
            this.endStates.states.profits = this.endStates.states.profits.substr(1);
            this.endStates.states.profits = parseInt(this.endStates.states.profits);
            if (this.endStates.states.profits < 0) {
                negative = true;
                this.endStates.states.profits *= -1;
            }
            let log = Math.floor(Math.log10(this.endStates.states.profits));
            this.endStates.states.profits /= Math.pow(10, log-2);
            this.endStates.states.profits = Math.round(this.endStates.states.profits);
            this.endStates.states.profits *= Math.pow(10, log-2);
            // Adds thousand-separating commas.
            // Based on: https://stackoverflow.com/a/2901298/313273
            this.endStates.states.profits = this.endStates.states.profits.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            this.endStates.states.profits = `${negative ? '-' : ''}$${this.endStates.states.profits}`
            let states = this.story.variablesState;
            this.endStates.states.platforms = [
                {name: "PC", on: states.pc},
                {name: "Console", on: states.console},
                {name: "Next Gen", on: states.next_gen}
            ];
        },
        /**
         *Restart the game from the start.
         */
        _restart: function() {
            this.story.ResetState();
            this.sheetHeight = null;
            this.currentText = [{id: 0, text: ''}];
            this.currentTags = new Set();
            this.background = 'title';
            this.morale = 'high';
            this.backgroundExtension = '';
            this.fullBackground = 'title.png';
            this.character = '';
            this.characterVisible = false;
            this.itemName = '';
            this.itemNameVisible = false;
            this.isOver = false;
            this.isPlay = true;
            this.endStates = {};
            this.advance();
        },
        /**
         * Preload images for fast use later.
         * Does not work for images loaded before the preload function has finished completing.
         */
        preload: function() {
            let that = this;
            axios.get(`${process.env.BASE_URL}preload.txt?)${Math.floor(Math.random() * 100000000)}`)
                        .then (function(response) {
                            let images = response.data.split('\n');
                            images = images.map(s => s.indexOf('//') === -1 ? s : s.substring(0, s.indexOf('//') - 1))
                                .map(s => s.trim()).filter(s => s != '');
                            Preload(images, {
                                onSingleImageComplete: function() {that.progressValue += 1;},
                                onComplete: function() {that.imagesLoaded = true; that.advance();}});
                        });
        },
        _setEndGame: function(text) {
            text = text.substr(ENDGAME_TEXT.length);
            let states = {};
            let values = text.split(';').map((v) => v.trim());
            ENDING_ARRAY.forEach((key, value) => states[key] = values[value]);
            let achievements = [];
            states.achievements = states.achievements.split('%').map((v) => v.trim()).filter((v) => v.length > 0);
            for(let i = 0; i < states.achievements.length; i++) {
                achievements.push({
                        name: states.achievements[i],
                        description: states.achievements[i + 1]
                    });
                i += 1;
            }
            delete states.achievements;
            this.endStates = {states, achievements};
            console.log(this.endStates);
        },
        /**
         * Returns the CSS rules that match an elements.
         * Taken from https://stackoverflow.com/a/37958301 .
         */
        __getMatchingCssRules: function(el) {
            var sheets = document.styleSheets, ret = [];
            el.matches = el.matches || el.webkitMatchesSelector || el.mozMatchesSelector 
                || el.msMatchesSelector || el.oMatchesSelector;
            for (var i in sheets) {
                var rules = sheets[i].rules || sheets[i].cssRules;
                for (var r in rules) {
                    if (el.matches(rules[r].selectorText)) {
                        ret.push(rules[r].cssText);
                    }
                }
            }
            return ret;
        },
        _test: function() {
            this.endStates = {
                achievements:
                    [{
                        name: "Walk the Plank",
                        description: "Your people hate you so much they become mutinous and demand your head on a platter."}],
                states:{
                    profits: "$12,300,000",
                    morale_state: "lorem ipsum",
                    budget_state: "dolor si amet",
                    platforms: [
                        {name: "fake data", on: true},
                        {name: "not real", on: false},
                        {name: "imaginary", on: false}
                    ],
                    general_description: "This is completely made up.",
                    releases: "You delayed your game for 9 months.",
                    crunch: "It was a tough slog but your people were glad they didn't have to sacrifice their personal lives.",
                    scandals:"No scandals here, good job."
                }
            };
        },
        gotoNewWindow: function(uri) {
            window.open(uri, '_blank');
        },
    },
    mounted: function() {
        this.isMounted = true;
        let that = this;
        if (!this.mounted) {
            axios.get(`${process.env.BASE_URL}story.json?)${Math.floor(Math.random() * 100000000)}`)
                .then (function(response) {
                        let storyText = JSON.stringify(response.data);
                        let storyObject = new Story(storyText);
                        that.story = storyObject;
                        that._test();
                        that.preload();
                });
        }
    },
}
</script>

